/* Careers Page Styles */
/* Careers Page Styles */
.hero {
    background: transparent;
    /* Changed from dark */
    color: var(--color-gray-900);
    /* Changed from white */
    padding: 60px 24px 80px;
    text-align: center;
    border-radius: 0;
    margin-bottom: 0px;
    position: relative;
    overflow: hidden;
}

.heroContent {
    position: relative;
    z-index: 10;
    max-width: 800px;
    margin: 0 auto;
}

.heroTitle {
    font-size: 48px;
    font-weight: 800;
    margin-bottom: 24px;
    line-height: 1.2;
    letter-spacing: -1px;
}

.heroDescription {
    font-size: 18px;
    line-height: 1.6;
    color: var(--color-gray-600);
    /* Changed from light gray */
    max-width: 600px;
    margin: 0 auto;
}

.accent {
    background: linear-gradient(90deg, var(--color-primary-light), var(--color-secondary-light));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: transparent;
    /* Fallback */
}

/* Filters */
.filtersContainer {
    display: flex;
    justify-content: center;
    gap: 12px;
    flex-wrap: wrap;
    background: white;
    padding: 16px;
    border-radius: 100px;
    box-shadow: var(--shadow-lg);
    max-width: fit-content;
    margin: -80px auto 40px;
    position: relative;
    z-index: 20;
    border: 1px solid var(--color-gray-100);
}

.filterBtn {
    padding: 10px 24px;
    border-radius: 50px;
    border: 1px solid transparent;
    background: transparent;
    color: var(--color-gray-500);
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
    transition: all var(--transition-fast);
}

.filterBtn:hover {
    color: var(--color-gray-900);
    background: var(--color-gray-50);
}

.filterBtn.active {
    background: var(--color-gray-900);
    color: white;
}

/* Jobs Grid */
.jobsGrid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 24px;
    max-width: 1200px;
    margin: 0 auto 60px;
    padding: 0 24px;
}

.jobCard {
    background: white;
    border-radius: var(--radius-xl);
    padding: 32px;
    box-shadow: var(--shadow-sm);
    border: 1px solid var(--color-gray-200);
    transition: all var(--transition-fast);
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
}

.jobCard:hover {
    transform: translateY(-4px);
    box-shadow: var(--shadow-lg);
    border-color: var(--color-primary);
}

.departmentBadge {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    padding: 6px 12px;
    background: var(--color-gray-50);
    border-radius: var(--radius-full);
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    color: var(--color-gray-600);
    margin-bottom: 20px;
    width: fit-content;
}

.jobTitle {
    font-size: 20px;
    font-weight: 700;
    color: var(--color-gray-900);
    margin-bottom: 12px;
    line-height: 1.4;
}

.jobMeta {
    display: flex;
    align-items: center;
    gap: 12px;
    color: var(--color-gray-500);
    font-size: 14px;
    margin-bottom: 20px;
}

.jobDescription {
    color: var(--color-gray-600);
    line-height: 1.6;
    margin-bottom: 24px;
    flex: 1;
}

/* Requirements List in Card */
.tags {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-bottom: 24px;
}

.tag {
    font-size: 12px;
    padding: 4px 10px;
    background: var(--color-gray-50);
    color: var(--color-gray-600);
    border-radius: 6px;
    border: 1px solid var(--color-gray-100);
}

.applyBtn {
    width: 100%;
    padding: 14px;
    background: var(--color-gray-900);
    color: white;
    border: none;
    border-radius: var(--radius-lg);
    font-weight: 600;
    font-size: 15px;
    cursor: pointer;
    transition: background var(--transition-fast);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    text-decoration: none;
}

.applyBtn:hover {
    background: var(--color-primary);
}

/* Empty State */
.emptyState {
    text-align: center;
    padding: 60px 0;
    color: var(--color-gray-500);
}

.emptyLink {
    color: var(--color-primary);
    background: none;
    border: none;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    text-decoration: underline;
    margin-top: 12px;
}

/* Modal */
.modalOverlay {
    position: fixed;
    inset: 0;
    background: rgba(15, 23, 42, 0.6);
    backdrop-filter: blur(4px);
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    animation: fadeIn 0.2s ease;
}

.modalContent {
    background: white;
    border-radius: 24px;
    width: 100%;
    max-width: 600px;
    padding: 40px;
    position: relative;
    box-shadow: var(--shadow-xl);
    animation: slideUp 0.3s ease;
}

.closeBtn {
    position: absolute;
    top: 24px;
    right: 24px;
    background: var(--color-gray-100);
    border: none;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-gray-500);
    transition: all var(--transition-fast);
    font-size: 18px;
}

.closeBtn:hover {
    background: var(--color-gray-200);
    color: var(--color-gray-900);
}

.modalTitle {
    font-size: 28px;
    font-weight: 800;
    color: var(--color-gray-900);
    margin-bottom: 8px;
}

.modalSubtitle {
    color: var(--color-gray-500);
    margin-bottom: 32px;
}

.emailCta {
    background: var(--color-gray-50);
    border: 1px dashed var(--color-gray-300);
    border-radius: var(--radius-lg);
    padding: 24px;
    text-align: center;
    margin-bottom: 24px;
}

.emailLink {
    display: inline-block;
    background: var(--color-primary);
    color: white;
    padding: 16px 32px;
    border-radius: var(--radius-lg);
    font-weight: 700;
    text-decoration: none;
    margin-top: 16px;
    box-shadow: var(--shadow-md);
    transition: transform var(--transition-fast);
}

.emailLink:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-lg);
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes slideUp {
    from {
        transform: translateY(20px);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}